import axios from 'axios';
import React, { useState } from 'react';
import image from "../../images/bg-login.png"

export const Login = () => {
  const [user, setUser] = useState({
    name: '',
    password: '',
  });

  const onChangeInput = e => {
    const { name, value } = e.target;
    setUser(prevState => ({ ...prevState, [name]: value }));
  };

  const loginSubmit = async e => {
    e.preventDefault();
    try {
      const res = await axios.post('http://89.116.52.58:3001/user/login', user);
      localStorage.setItem('name', res.data.name);
      window.location.href = '/';
    } catch (err) {
      alert(err.response.data.msg);
    }
  };
  return (
    // <Grid container direction="row" sx={{ justifyContent: 'center', alignItems: 'center', paddingTop: '18vh', paddingBottom:'18vh' }} >
    //       <Card className="login-card" >
    //           <Stack paddingTop={"10%"} className='login-stack' spacing={4}>
    //               <form onSubmit={loginSubmit}>
    //               <Typography sx={{ color: 'black', textAlign: 'center', fontSize: '26px', padding:'1rem' }}>ye Girii</Typography>
    //               <TextField label="Kullanc Ad" variant="outlined" type="text" name="name" required onChange={onChangeInput} 
    //                   sx={{  width:'90%' }} />
    //               <TextField label="Parola" variant="outlined" type="password" name="password" required onChange={onChangeInput} 
    //                   sx={{ width: '90%', margin:'0.5rem' }} />
    //               <Button sx={styles.button} type='submit'>Giris Yap</Button>
    //               </form>
    //           </Stack>
    //       </Card>
    //   </Grid>
    <div class="container">
      <div class="login__content">
        <img src={image} alt="girisimage" class="login__img" />

        <form action="" onSubmit={loginSubmit} class="login__form">
          <div>
            <h1 class="login__title">
              <span>Hogeldiniz!</span>
            </h1>
            <p class="login__description">
              Lften giri yapp devam ediniz.
            </p>
          </div>

          <div>
            <div class="login__inputs">
              <div>
                <label for="" class="login__label">Kullanc Ad</label>
                <input type="text" placeholder="Kullanc adnz giriniz." name="name" required onChange={onChangeInput} className="login__input" />
              </div>

              <div>
                <label for="" class="login__label">ifre</label>

                <div class="login__box">
                  <input type="password" placeholder="ifrenizi giriniz." required name="password" onChange={onChangeInput} class="login__input" id="input-pass" />
                  <i class="ri-eye-off-line login__eye" id="input-icon"></i>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div class="login__buttons">
              <button type="submit" class="login__button">Giri Yap</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  )
};
export default Login;